<template>
    <div class="homesubjectone">
        <div class="content">
            <div class="content-left">
                <ul>
                    <li @click="goState(item.routerpush)" v-for="(item, index) in leftMessage" :key="index">
                        <div :class="item.iconclass" class="icon">
                            <i :class="item.classname"></i>
                            <span v-if="item.text==='错题本'" class="tips">{{locationMessage.ErrorBook}}</span>
                        </div>
                        <span>{{item.text}}</span>
                    </li>
                </ul>
            </div>
            <div class="content-center">
                <div class="Sequential-practice">
                    <div class="route-practice">
                        <canvas @click="canvas1fn()" width="150" height="150" ref="canvas1" id="canvas1"></canvas>
                    </div>
                </div>
                <div class="Mock-exam">
                    <div class="route-exam">
                        <canvas @click="canvas2fn()" width="150" height="150" ref="canvas2" id="canvas2"></canvas>
                    </div>
                </div>
            </div>
            <div class="content-right">
                <ul>
                    <li @click="goState(item.routerpush)" v-for="(item, index) in rightMessage" :key="index">
                        <div :class="item.iconclass" class="icon">
                            <i :class="item.classname"></i>
                        </div>
                        <span>{{item.text}}</span>
                    </li>
                </ul>
            </div>
        </div>

        <div class="about">
            <div class="title">
                <h2><i class="fa fa-briefcase"></i> 拿本利器</h2>
            </div>
            <div class="about-content">
                <div class="card-groups">
                    <ul>
                        <li v-for="(item, index) in artifactMessage" :key="index">
                            <h6 :class="[item.active?'active':'']">{{item.title}}</h6>
                            <p>{{item.content}}</p>
                        </li>
                    </ul>
                </div>
            </div>
        </div>

    </div>
</template>

<style lang="less">
    //动画效果 
    @keyframes plus {
        0% {
            transform: scale(0) rotate(180deg);
        }

        50% {
            transform: scale(1) rotate(0deg);
        }

        100% {
            transform: scale(1) rotate(90deg)
        }
    }

    //公共样式
    .content-li {
        li {
            font-size: 2rem;
            flex-basis: 100%;
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            margin: 1.5rem 0;
            position: relative;
    
            .icon {
                position: relative;
                width: 50%;
                text-align: center;
                padding: 1rem 0.2rem;
                border-radius: 1rem;
                font-size: 3rem;
                color: rgb(240, 235, 235);
.tips{
        position: absolute;
        color: black;
      text-align: center;
      top: -1rem;
      background: red;
      color: white;
      font-weight: bold;
      border-radius: 50%;
      font-size: 1.3rem;
      width: 2.5rem;
      height:2.5rem;
      line-height: 2.5rem;
    }
                &.plus {
                    background: rgba(243, 188, 6, 0.829);

                    i {
                        animation: plus 1s infinite alternate-reverse;
                    }
                }

                &.hunt {
                    background: rgba(9, 177, 243, 0.836);
                }

                &.board {
                    background: rgba(223, 59, 9, 0.822);
                }

                &.book {
                    background: rgb(202, 5, 228);
                }

                &.random {
                    background: rgb(8, 110, 243);
                }

                &.eye {
                    background: rgb(0, 195, 255);
                }

                &.hand-grab-o {
                    background: rgb(238, 192, 94);
                }

                &.star-o {
                    background: rgb(12, 243, 212);
                }
            }

            span {
                width: 100%;
                text-align: center;
            }
        }
    }

    .homesubjectone {
        background: rgb(235, 231, 231);

        .content {
            background: rgb(255, 255, 255);
            display: flex;

            .content-left {
                flex-basis: 100%;

                ul {

                    display: flex;
                    flex-wrap: wrap;
                    .content-li;
                }
            }

            .content-center {
                display: flex;
                flex-basis: 100%;
                flex-wrap: wrap;
                align-content: space-around;
                text-align: center;
                font-size: 1.5rem;

                canvas {
                    cursor: pointer;
                }

                .Mock-exam {
                    display: flex;
                    justify-content: center;
                    flex-basis: 100%;
                }

                .Sequential-practice {
                    display: flex;
                    justify-content: center;
                    flex-basis: 100%;
                }
            }

            .content-right {
                flex-basis: 100%;
                text-align: right;

                ul {
                    display: flex;
                    flex-wrap: wrap;
                    .content-li;
                }
            }
        }

        .about {
            margin: 1rem 0;
            background: white;

            .title {
                display: flex;
                padding: 1rem;

                h2 {
                    i {
                        color: green;
                    }

                    letter-spacing: 0.5rem;
                    font-weight: 600;
                }

                border-bottom: 0.1rem solid lightgray;
            }

            .about-content {
                .card-groups {
                    ul {
                        display: flex;
                        justify-content: space-around;
                        margin: 1rem 0;

                        li {
                            box-sizing: border-box;
                            padding: 1rem;
                            background: rgb(235, 231, 231);
                            flex-basis: 23%;
                            text-align: center;
                            cursor: pointer;

                            h6 {
                                font-size: 1.3rem;
                                margin: 0.5rem;

                                &.active {
                                    color: orange;
                                }
                            }

                            p {
                                font-size: 1rem;
                                color: rgb(85, 82, 82);
                            }
                        }
                    }
                }
            }
        }
    }
</style>

<script>
    export default {
        name: 'homesubjectone',
        data() {
            return {
                locationMessage:{
                    ErrorBook:window.localStorage.ErrorNum
                },
                artifactMessage: [{
                    title: '专属训练',
                    content: '定制拿本计划',
                    active: false
                }, {
                    title: 'VIP特权',
                    content: '练体秘籍',
                    activ: false
                }, {
                    title: '学车技巧',
                    content: '答题口诀',
                    active: false

                }, {
                    title: '全程砍价',
                    content: '百人享内部价',
                    active: true
                }, ],
                leftMessage: [{
                        text: 'VIP课程',
                        iconclass: 'plus',
                        classname: 'plus fa fa-search-plus'
                    },
                    {
                        text: '图标技巧',
                        iconclass: 'hunt',
                        classname: 'hunt fa fa-product-hunt'
                    },
                    {
                        text: '学车课堂',
                        iconclass: 'board',
                        classname: 'board fa fa-mortar-board',
                    },
                    {
                        text: '错题本',
                        iconclass: 'book',
                        classname: 'book fa fa-book',
                        routerpush: '/Errorbook'
                    },
                ],
                rightMessage: [{
                        text: '随机练习',
                        iconclass: 'random',
                        classname: 'random fa fa-random',
                    
                    },
                    {
                        text: '专项练习',
                        iconclass: 'eye',
                        classname: 'eye fa fa-eye'
                    },
                    {
                        text: '交警手势',
                        iconclass: 'hand-grab-o',
                        classname: 'hand-grab-o fa fa-hand-grab-o'
                    },
                    {
                        text: '收藏',
                        iconclass: 'star-o',
                        classname: 'star-o fa fa-star-o',
                        routerpush:'/Collection'
                    },
                ]
            }
        },
        methods: {
            canvas2fn() {
                this.$router.push('/Answer')
            },
            canvas1fn(){
                this.$router.push('/practice')
            },
            goState(pushurl) {
                if (pushurl === undefined) {
                    return
                }
                this.$router.push(pushurl)
            }
        },
        mounted() {
            let canvas1 = this.$refs.canvas1
            let canvas2 = this.$refs.canvas2
            let can1 = canvas1.getContext('2d')
            let can2 = canvas2.getContext('2d')
            drawfn(can1, '顺序练习', window.localStorage.PracticeNum+'/200', 'green')
            drawfn(can2, '模拟考试', window.localStorage.HighestScore + '分', '#6211ce')

            function drawfn(ctx, title, content, background) {
                ctx.fillStyle = "lightgray"
                ctx.arc(75, 75, 65, 0, 2 * Math.PI)
                ctx.fill()
                ctx.beginPath()
                ctx.fillStyle = "white"
                ctx.arc(75, 75, 55, 0, 2 * Math.PI)
                ctx.fill()
                ctx.beginPath()
                ctx.fillStyle = background
                ctx.arc(75, 75, 50, 0, 2 * Math.PI)
                ctx.fill()
                ctx.beginPath()
                ctx.textAlign = 'center'
                ctx.font = '2rem sans-serif'
                ctx.fillStyle = "white"
                ctx.fillText(title, 75, 75, 150);
                ctx.beginPath()
                ctx.textAlign = 'center'
                ctx.font = '1.5rem sans-serif'
                ctx.fillStyle = "white"
                ctx.fillText(content, 75, 100, 150)
            }
        },
    }
</script>